export default {
	props:{
		cartBasketRect:Object,// 购物车篮的rect信息
	},
	 data() { 
	  	return {
			offsetX: 0,
			offsetY: 0,
			ballX: 0,
			ballY: 0,
			balls: {},
			//避免抖动
			lastEvent:'',
			lastId:''
		}
	  },
	  created() {
		  	let balls = [];
		  	for (let i = 0; i < 5; i++) {
		  		balls.push({ inited: false });
		  	}
			this.balls = balls
	  },
	  methods:{
		  getBalls() {
		  	return balls;
		  },
		  addToCart (e,id) {
			if(!id){
			  this.lastId  = ''
			  return
			}

			const self = this
			if(this.lastId == id){
			  e = this.lastEvent
			}else{
			  this.lastId  = id
			  this.lastEvent = e
			}
			//((res.width + that.ballWidth ) / 2  - that.ballWidth);
			let ballX = e.left
			let	ballY = e.top
			this.offsetX = -Math.abs(this.cartBasketRect.left - ballX + 10)
			this.offsetY = Math.abs(this.cartBasketRect.top - ballY +(this.cartBasketRect.height/1.5))
			this.ballX = ballX
			this.ballY = ballY
				
			
			for (let i = 0; i < 5; i++) {
				if (!this.balls[i].inited) {
					this.balls[i].inited = true
						setTimeout(() => {
							self.balls[i].inited= false
						}, 500);
					break;
				}
			}
		  }
	  }
	  
	  
}
